<script>
  import { fade } from "svelte/transition";
  import { slide } from "./store.js";

  let score = 0;
  let isAnimated = false;
  let isVideo = false;
  let slides = [
    {
      video: "witcher",
      answerYes: "Ведьмак 3 – это практически Эверст игрового мира в своем жанре",
      answerNo:
