    },
    {
      video: "anime",
      answerYes: "Я посмотрел 200+ тайтлов, и считаю что аниме - это охуенно",
      answerNo: "Посмотрел около 300 - аниме говно с самоповтором"
    },
    {
      video: "genshin",
      answerYes:
        "Для бесплатной гача-дрочильни с вайфу Genshin Impact сделана очень хорошо. Уму не постижимо сколько человекочасов потрачено на разработку, прописывание лора и создание образов персонажей. Аниматорам, которые работали над некоторыми катсценами хочется пожать руки, потому что глядя на них создаётся впечатление, что смотришь довольно неплохое аниме",
      answerNo:
        "Genshin Impact - это даже не игра, а просто дешевый всратый мусор для выкачки денег"
    },
    {
      video: "padoru",
      answerYes:
        "Люди делятся духом падору друг с другом — и это прекрасно. Праздничная Неро всё уверенней становится доброй ежегодной традицией среди любителей аниме",
      answerNo:
        "Очередная завирусившаяся хуета, гнилой мем, презираю всех, у кого на аве падору"
    },
    // {
    //   video: "streamer",
    //   answerYes:
    //     "Стримеры — новые медиа-гиганты, которые получают миллионы за то, что играют в любимые игры и гуляют по паркам",
    //   answerNo:
    //     "Не знаю, вот эти стримеры для школоты... И ник, и харя туповатая, манеры жлобские, самодовольный типок — буэээ не люблю таких"
    // },
    {
      video: "gothic",
      answerYes:
        "Готика 2 - это как Терминатор 2. Больше, масштабней, дороже, шире, глубже, жирней и в то же время остающейся - ОХРЕНИТЕЛЬНОЙ!",
      answerNo:
        "скачал эту вашу готику 2, потыкал да и чето кал какой-то, кто создавал управление? ниче непонятно, сливает первый же волк, че так сложно? еще и графика какой-то пластилиновый кусок кала, кто вообще в такое играет"
    },
    {
      video: "elden",
      answerYes:
        "Elden Ring точно удалась, а всё долгое томительное ожидание было не зря. Она создана с чётким пониманием того, за что фанаты полюбили серию Souls, эксплуатирует её самые сильные стороны и оттачивает плюсы. При этом трудно представить лучшую точку входа в жанр, чем Elden Ring — игру большую, в меру сложную и невероятно увлекательную.",
      answerNo:
        "Один тот факт, что Elden Ring получила 97 баллов на Metacritic, говорит о том, что рецензентам безразличен пользовательский интерфейс. И стабильная производительность на ПК... И, по правде, дизайн квестов"
    },
    {
      video: "godofwar",
      answerYes:
        "Я с трепетом ждал Ragnarök — и в итоге получил ровно то, чего хотел. Большое приключение с по-настоящему живыми и убедительными персонажами, к которым быстро привязываешься. Однако на одних лишь главных героях авторы не остановились, ведь у каждого из второстепенных персонажей, от Фреи до гномихи Лунды, есть свой характер и харизма. Чего только стоит Один в исполнении Ричарда Шиффа («Западное крыло»), каждое появление которого в кадре — отдельное событие.",
      answerNo:
        "Ну, сони посмотрели как скакнула популярность у мгр и дмс за счет мемов и решили... сделать ковейер из мемов. Знаете, есть какая-то сверх мерзотность в таком подходе. Когда игра настолько никчемна и уныла в своей сути, в ней нет ничего запоминающегося. Ни музыки, ни катсцен, ни геймплея, просто нихуйя. Игра посредственность. И из этого ковейерного кала пытаются вылепить КОНВЕЙЕРНЫЕ МЕМЫ БЛЯДЬ!!1 Какая же это гнусная хуйня. Это буквально та же самая ситуация, когда богатый папаша пытается вылепить из своего отпрыска знаменитость, но отпрыск настолько бездарный обмудок, что ему ничего не поможет. Вот это и есть новый год оф вар, вот это и есть подход к играм сони. Блядь, я никогда не думал, что они настолько скатятся, хуже уже просто некуда."
    },
    {
      video: "horizon",
      answerYes:
        "Horizon Forbidden West — самый амбициозный проект PlayStation на сегодняшний день. Настолько больших и комплексных игр Sony ещё никогда не делала, а первая часть на фоне сиквела кажется лишь грубым наброском.",
      answerNo:
        "Horizon Forbidden West - созданный нейросетью бездарный клон асассинов с самым пресным в истории геймплеем и сюжетом. Не стоит внимания от слова совсем"
    },
    {
      video: "rdr",
      answerYes:
        "Как по мне, RDR2 просто напросто лучшая игра последних десяти лет минимум. Настоящий шедевр, аналог которого мы вряд ли увидим в обозримом будущем, если увидим вообще.",
      answerNo:
        "Ее медлительность не дает ничего, кроме растягивания игры и нежелания в нее играть, как вспомнишь какие там медленные анимации. Игра для школьников, у которых сотни часов свободного времени"
    },
    {
      video: "cyberpunk",
      answerYes:
        "бля, Cyberpunk 2077 вышла охуенной. Прям то, что я ожидал! Мои глаза просто получали огразмы от красот Найт сити, геймплея, музыки, сюжетки, да даже второстепенные задания хороши, молчу уже про персонажей. Они все запоминающие. Чего стоит чувак у которого вместо носа граната и парня, который установил имплант себе в хер и он прям дымился.",
      answerNo:
        "Киберпанку не место в топах - это чисто коммерческий скам на гране мошенничества, этот продукт должен остаться примером крупным издателям как не надо делать игры. Сидипыры не сделали ничего чтобы сделать игру лучше, она до сих пор багует, она до сих пор на пастгене выглядит так, что лучше бы не выходила, она и близко не та которой ее малевали продажные блогеры и журнализы и никогда такой не будет."
    }
  ];
  let resultsText = [
    "Поздравляю! Вы порвались. Полностью.",
    "Поздравляю! Вы очень легко рвётесь, но еще остались вещи, которыми вы способны наслаждаться.",
    "Поздравляю! Вы легко рвётесь, но в мире осталось ещё много вещей от которых вы способны получать удовольствие.",
    "Поздравляю! Вы - нормис. Есть вещи, которые вам не нравятся, но это нормально и применимо к большинству людей.",
    "Поздравляю! Вы практически невозмутимы. Есть пара неприятных моментов, но они не сильно выбивают вас из колеи.",
    "Поздравляю! Вы полностью невозмутимы и просто наслаждаетесь жизнью."
  ];
  let resultsArray = [0, 1, 3, 5, 8, 10];
  let result = 0;

  function calculateResult() {
    for (let i = 0; i < resultsArray.length; i++) {
      if (i !== resultsArray.length - 1) {
        if (resultsArray[i] <= score && resultsArray[i + 1] > score) {
          result = i;
          return;
        }
      } else {
        result = i;
      }
    }
  }

  function checkLastSlide() {
    if ($slide === slides.length + 1) {
      calculateResult();
    }
  }

  function goNextSlide() {
    if (!isAnimated) {
      isAnimated = true;
      setTimeout(() => {
        isVideo = false;
        slide.increment();
        checkLastSlide();
        isAnimated = false;
      }, 500);
    }
  }

  function chooseYes() {
    if (!isAnimated) {
      isAnimated = true;
      score += 1;
      setTimeout(() => {
        slide.increment();
        checkLastSlide();
        isAnimated = false;
      }, 500);
    }
  }

  function chooseNo() {
    if (!isAnimated) {
      isAnimated = true;
      setTimeout(() => {
        isVideo = true;
        isAnimated = false;
      }, 500);
    }
  }
</script>

<main>
  {#if isAnimated === false}
    {#if $slide === 0}
      <div class="slide" transition:fade>
        <h1 class="title">Порвался? Жаль</h1>
        <div class="subtitle">Пройдите тест и узнайте насколько сильно вы порветесь.<br><br>Выбирайте наиболее близкий вам вариант ответа.</div>
        <button class="button" on:click={goNextSlide}>Начать</button>
      </div>
    {:else if $slide > 0 && $slide <= slides.length}
      {#if isVideo === false}
        <div class="slide slide--question" transition:fade>
          <div class="answer">
            <div class="quote" on:click={chooseYes}>{slides[$slide - 1].answerYes}</div>
          </div>
          <div class="answer">
            <div class="quote" on:click={chooseNo}>{slides[$slide - 1].answerNo}</div>
          </div>
        </div>
      {:else}
        <div class="slide" transition:fade>
          <div class="video-wrapper">
            <div class="video-container">
              <video autoplay controls loop muted playsinline class="video">
                <source src="./video/{slides[$slide - 1].video}.mp4" type="video/mp4" />
              </video>
            </div>
          </div>
          <button class="button" on:click={goNextSlide}>Далее</button>
        </div>
      {/if}
    {:else}
      <div class="slide" transition:fade>
        <div class="result-img-container">
          <img src="./results/{result}.webp" alt="" class="result-img" />
        </div>
        <div class="title">{score} / {slides.length}</div>
        <div class="result-text">{resultsText[result]}</div>
      </div>
    {/if}
  {/if}
</main>

